.container {
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 320px) {
    max-width: 320px;
    padding: 0 20px;
  }
  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding: 0 75px;
  } /*без padding 618px*/
  @media screen and (min-width: 1024px) {
    max-width: 1024px;
    padding: 0 71px;
  }
} /*без padding 882px*/
